
export default function mainHeader() {
// Seleciona o header da navegação principal
    const mainHeader = document.querySelector('.header');
    
    // Adiciona um listener para o evento de rolagem
    window.addEventListener('scroll', () => {
        // Verifica a posição da rolagem
        if (window.scrollY > 50) { // 50 pixels é a sua 'distância'
            // Se a rolagem for maior que 50px, adiciona a classe 'scrolled'
            mainHeader.classList.add('scrolled');
        } else {
            // Se a rolagem for menor que 50px, remove a classe
            mainHeader.classList.remove('scrolled');
        }
    });
}