 
    export default function () {
    const sliderContainer = document.querySelector('.slider-container');
    const slides = document.querySelectorAll('.slide');
    //const slides_ativo = document.querySelector('.slide.active');
    const navButtons = document.querySelectorAll('.nav-btn');
    const dotsContainer = document.querySelector('.dots-container');
    const dots = document.querySelectorAll('.dot');
    const totalSlides = slides.length;
    let currentSlide = 0;
    let slideInterval ;

    //touch screen
    let touchStartX = 0;
    let touchEndX = 0;
    const minSwipeDistance = 50; // Distância mínima (em pixels) para considerar um swipe
    //let isHovering = false; // Adicione esta variável para rastrear o estado do mouse

   2 // Função para mostrar o slide atual
    function showSlide(index) {
        // Remove a classe 'active' de todos os slides e pontos
        slides.forEach(slide => slide.classList.remove('active'));
        dots.forEach(dot => dot.classList.remove('active'));

        // Adiciona a classe 'active' ao slide e ponto corretos
        slides[index].classList.add('active');
        dots[index].classList.add('active');
    }

    // Função para ir para o próximo slide
    function nextSlide() {
        currentSlide = (currentSlide + 1) % totalSlides;
        showSlide(currentSlide);
    }

    // Função para iniciar a transição automática
    function startAutoSlide() {
         // Só inicia o autoplay se o mouse não estiver sobre o slider
        slideInterval = setInterval(nextSlide, 5000);
    
    }

    // Função para parar a transição automática
    function stopAutoSlide() {
        clearInterval(slideInterval);
    }

    // Inicializa o slider
    showSlide(currentSlide);
    startAutoSlide();

    // Adiciona eventos de clique aos botões de navegação
    navButtons.forEach(button => {
        button.addEventListener('click', () => {
            stopAutoSlide(); // Para o autoplay
            const isNext = button.classList.contains('next');
            if (isNext) {
                currentSlide = (currentSlide + 1) % totalSlides;
            } else {
                currentSlide = (currentSlide - 1 + totalSlides) % totalSlides;
            }
            showSlide(currentSlide);
            //startAutoSlide(); 
        });
    });

    // Adiciona eventos de clique aos pontos indicadores
    dots.forEach((dot, index) => {
        dot.addEventListener('click', () => {
            stopAutoSlide(); // Para o autoplay
            currentSlide = index;
            showSlide(currentSlide);
            //startAutoSlide(); // Reinicia o autoplay
        });
    });



    // Para o autoplay quando o mouse passa sobre o slider
    sliderContainer.addEventListener('mouseenter', () => {
        
        stopAutoSlide();
    });
    sliderContainer.addEventListener('mouseleave', () => {
       
        startAutoSlide();
    });

    //Touch screen:

    // Adiciona o listener para quando o toque começa
sliderContainer.addEventListener('touchstart', (e) => {
    // Pega a posição X do primeiro dedo na tela
    touchStartX = e.touches[0].clientX;
});

// Adiciona o listener para quando o toque termina
sliderContainer.addEventListener('touchend', (e) => {
    // Pega a posição X do dedo quando ele é levantado
    touchEndX = e.changedTouches[0].clientX;
    handleSwipe(); // Chama a função que vai lidar com o gesto
});

// Função para lidar com o gesto de "arrastar"
function handleSwipe() {
    // Verifica se a distância percorrida é suficiente para um swipe
    if (Math.abs(touchEndX - touchStartX) < minSwipeDistance) {
        return; // Sai da função se a distância for pequena
    }
    
    // Se o dedo foi arrastado para a esquerda (próximo slide)
    if (touchEndX < touchStartX) {
        currentSlide = (currentSlide + 1) % totalSlides;
    } 
    // Se o dedo foi arrastado para a direita (slide anterior)
    else if (touchEndX > touchStartX) {
        currentSlide = (currentSlide - 1 + totalSlides) % totalSlides;
    }
    
    showSlide(currentSlide);
    startAutoSlide(); // Opcional: Reinicia o autoplay após a navegação manual
}

// Lembre-se de remover o `slides_ativo` das suas variáveis, pois ele não é necessário aqui.
// O restante do seu código (as funções `showSlide`, `startAutoSlide`, etc.) continua o mesmo.

};
 